const string = `.skin *{ margin: 0; padding: 0;box-sizing: border-box;}
.skin *::before{box-sizing: border-box;}
.skin *::after{box-sizing: border-box;}

body{
    background-color:#8ddbbd;
}
.skin{
    border-radius: 40%;
    width: 200px;
    height: 200px;
    left: 50%;
    margin-left: -100px;
    top: 50px;
    position: relative;
}
.face{
    border: 3px solid #dadada;
    position: absolute;
    width: 200px;
    height: 210px;
    left: 50%;
    margin-left: -100px;
    background-color: #faf8ff;
    border-radius: 90% 90% 75% 75%;
}
.nose{
    position: absolute;
    width: 18px;
    height: 18px;
    left:50%;
    top:112px;
    margin-left: -9px;
    border-radius: 60% 20% 20% 20%;
    transform:rotate(45deg);
    background-color: #020202;
}
.lip.left{
    border:3px solid #020202;
    position: absolute;
    width: 24px;
    height: 20px;
    left:50%;
    top:128px;
    border-radius: 0% 0% 0% 80%;
    border-top: none;
    border-right: none;
}
.lip.right{
    border:3px solid #020202;
    position: absolute;
    width: 24px;
    height: 20px;
    left:50%;
    top:128px;
    margin-left: -24px;    
    border-radius: 0% 0% 80% 0%;
    border-top: none;
    border-left: none;
}
.eye{
    border: 1px solid #a7a29d;
    position: absolute;
    width: 46px;
    height: 70px;
    left: 50%;
    top:40px;
    margin-left: -23px;
    background-color: #020202;    
}
.eye.left{
    border-radius: 45% 60%;
    transform:translateX(-48px)  rotate(35deg);
}
.eye.right{
    border-radius: 60% 45%;
    transform: translateX(48px) rotate(-35deg);
}
.eye.left::before{
    content: '';
    display: block;
    border:2px solid #a7a29d;
    position: absolute;
    background: #fff;
    width: 24px;
    height: 24px;
    left: 10px;
    top:12px;
    border-radius: 50%;
}
.eye.right::before{
    content: '';
    display: block;
    border:2px solid #a7a29d;
    position: absolute;
    background: #fff;
    width: 24px;
    height: 24px;
    right: 10px;
    top:12px;
    border-radius: 50%;   
}
.ear{
    border: 3px solid #6a9a8b;
    position: absolute;
    width: 80px;
    height: 80px;
    left: 50%;
    margin-left: -40px;
    background-color: #020202;
    border-radius: 50%;
}

.ear.left{
    transform: translateX(-80px);
}
.ear.right{
    transform: translateX(80px);
}

@keyframes wave{
    0%{
        transform: rotate(0deg);
    }
    33%{
        transform: rotate(5deg);
    }
    66%{
        transform: rotate(-5deg);
    }
    100%{
        transform: rotate(0deg);
    }
}
.skin:hover{
    transform-origin: 50% 100%;
    animation: wave 600ms infinite linear;
}`;

export default string;
